.labeledIcon {
  vertical-align: middle;
}
.labeledIcon[disabled] {
  cursor: default;
}
.labeledIcon.inline {
  display: inline-flex;
}
.labeledIcon.above,
.labeledIcon.below {
  height: auto;
}
.labeledIcon.above .iconCell,
.labeledIcon.before .iconCell,
.labeledIcon.left .iconCell,
:global(.enact-locale-right-to-left) .labeledIcon.right .iconCell {
  order: 2;
}
.labeledIcon.above .label,
.labeledIcon.before .label,
.labeledIcon.left .label,
:global(.enact-locale-right-to-left) .labeledIcon.right .label {
  order: 1;
}
:global(.enact-locale-right-to-left) .labeledIcon.left .iconCell {
  order: 1;
}
:global(.enact-locale-right-to-left) .labeledIcon.left .label {
  order: 2;
}
