:global .debug.spotlight .spottable-next-up::after,
:global .debug.spotlight .spottable-next-right::after,
:global .debug.spotlight .spottable-next-down::after,
:global .debug.spotlight .spottable-next-left::after {
  content: '';
  position: absolute;
  top: 0;
  border-width: 0 1px 1px 0;
  border-style: solid;
  font-size: 15px;
  line-height: 18px;
  padding: 0 1ex;
  left: 0;
  border-width: 0 1px 1px 1px;
  border-bottom-right-radius: 0.5ex;
  border-bottom-left-radius: 0.5ex;
  left: 50%;
  transform: translateX(-50%);
}
:global .debug.spotlight .spottable-next-up {
  box-shadow: 0 12px 9px -3px hsl(0, 100%, 72%), 0 0 1px 3px hsl(0, 100%, 72%);
}
:global .debug.spotlight .spottable-next-up::after {
  content: 'Spotlight next: up';
  color: hsl(0, 100%, 72%);
}
:global .debug.spotlight .spottable-next-right {
  box-shadow: -12px 0 9px -3px hsl(90, 100%, 72%), 0 0 1px 3px hsl(90, 100%, 72%);
}
:global .debug.spotlight .spottable-next-right::after {
  content: 'Spotlight next: right';
  color: hsl(90, 100%, 72%);
}
:global .debug.spotlight .spottable-next-down {
  box-shadow: 0 -12px 9px -3px hsl(180, 100%, 72%), 0 0 1px 3px hsl(180, 100%, 72%);
}
:global .debug.spotlight .spottable-next-down::after {
  content: 'Spotlight next: down';
  color: hsl(180, 100%, 72%);
}
:global .debug.spotlight .spottable-next-left {
  box-shadow: 12px 0 9px -3px hsl(240, 100%, 72%), 0 0 1px 3px hsl(240, 100%, 72%);
}
:global .debug.spotlight .spottable-next-left::after {
  content: 'Spotlight next: left';
  color: hsl(240, 100%, 72%);
}
